import React from "react";
import ReactDOM from "react-dom";
import { HashRouter } from "react-router-dom";

import App from "./App";
import { CartContextProvider } from "./store/cart-context";

import "./index.css";

ReactDOM.render(
  <CartContextProvider>
    <HashRouter>
      <App />
    </HashRouter>
  </CartContextProvider>,
  document.getElementById("root")
);
